.day {
  @include day($size);
  background-color: $day-color;
  border: 1px solid black;
  box-sizing: border-box;
  text-align: center;
  line-height: $size;
  transition: all 0.5s;

  &::before {
    font-size: 30px;
    font-family: Arial, serif;
  }

  &:hover {
    background-color: pink;
    cursor: pointer;
    transform: translateY(-$day-move-on-hover); 
    transition: all 0.5s;
  }
}