.calendar {
  padding: 2 * $padding $padding;
  width: $calendar-size;
  display: flex;
  flex-wrap: wrap;
  gap: $gap $gap;
  background-color: aliceblue;
  margin: auto;

  @for $dayNum from 1 through 31 {
    & > :nth-child(#{$dayNum}){
      &::before {
        content: "#{$dayNum}";
      }
    }
  }

  @each $startDay, $margin-left in $days {
    &--start-day--#{$startDay} > :first-child {
      margin-left: $margin-left;
    }
  }

  @for $lastDay from 28 to 31 {
    &--month-length--#{$lastDay} > :nth-last-child(-n + #{31 - $lastDay}) {
        display: none;
      }
  }
}
